.c-contact-wrapper {
    position: relative;
    padding-top: get-vw(332px);

    @include mq(md) {
        margin-top: 0;
    }

    @include mq(sm) {
        .o-section {
            padding-top: 0;
        }
    }

    &__inner {
        position: relative;
    }

    &__contact {
        position: relative;
        display: block;
        z-index: 1;
    }

    &__model {
        position: absolute;
        top: -20%;
        left: 0;
        width: 100%;
        height: 0;
        padding-top: 100%;

        canvas {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }

    &__extra-big-text {
        @include fluidValue("margin-bottom", "s-248");
        width: percentage(19.5, 22);
        word-break: break-word;
        position: relative;
        z-index: 1;

        a {
            display: block;
        }

        @include mq(md) {
            max-width: 100%;
        }
    }
}
